.input {
  display: flex;
  width: 90%;
  position: relative;
  align-items: center;
}
.input__field {
  width: 100%;
  border-radius: 50px;
  padding: 20px 30px;
  font-size: 25px;
  border: none;
  transition: 0.2s;
  box-shadow: inset 0 0 5px black;
}

.input__field:focus {
  box-shadow: 0 0 10px 1000px rgba(0, 0, 0, 0.5);
  outline: none;
}
.input__button {
  position: absolute;
  width: 50px;
  height: 50px;
  margin: 12px;
  border-radius: 50px;
  right: 0px;
  border: none;
  font-size: 15px;
  background-color: #ffa07a;
  color: #fff;
  transition: 0.2s all;
  box-shadow: 0 0 10px black;
  cursor: pointer;
}

.input__button:hover {
  background-color: #ffb69a;
}

.input__button:active {
  transform: scale(0.8);
  box-shadow: 0 0 5px black;
}

@media (max-width: 700px) {
  .input {
    width: 95%;
  }
}
